@charset "UTF-8";
/* リキッドレイアウト対応 */
:root {
  --base-font-family: 'Zen Kaku Gothic New', sans-serif;
  --en-font: 'Europa', sans-serif;
  --padding-pc: 25px;
  --padding-sp: 20px;
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --black: #000;
  --white: #fff;
  --base-color: #1a1a1a;
  --bg-color: #ede5dc;
  --blue: #11a4d8;
  --gray: #d9d9d9;
  --yellow: #beba3f;
  --green: #94be6d;
  --pink: #d78580;
  --brown: #736357;
  --teal: #7fb8a6;
  --purple: #909ad3;
}

body {
  font-family: var(--base-font-family);
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
html {
  font-size: 16px;
}
@media (min-width: 768px) {
  html {
    font-size: 1.1111111111vw;
  }
}
@media (min-width: 1440px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: all;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  height: 100%;
  object-fit: cover;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

html {
  overflow-wrap: break-word;
}

.button {
  align-items: center;
  display: flex;
  font-family: var(--base-font-family);
  font-size: 0.875rem;
  font-weight: var(--bold);
  color: var(--base-color);
  gap: 1.5625rem;
  line-height: normal;
  width: fit-content;
  padding: 0.8125rem 0;
  border-bottom: 1px solid var(--base-color);
  position: relative;
  transition: 0.3s ease-out all;
  overflow: visible;
}

.button__icon {
  aspect-ratio: 28/13;
  width: 0.8125rem;
  background-color: var(--base-color);
  mask-image: url("../images/arrow.svg");
  mask-repeat: no-repeat;
  mask-size: contain;
  mask-position: center;
  position: relative;
  z-index: 2;
  transition: 0.3s ease-out all;
}

.button::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  translate: 50% -50%;
  width: 1.5625rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 1px solid var(--yellow);
  z-index: 3;
  right: 0.4375rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

@media (any-hover: hover) {
  .button:hover {
    border-bottom-color: var(--yellow);
    color: var(--yellow);
  }
  .button:hover span {
    background-color: var(--yellow);
    transform: translateX(0.3125rem);
  }
  .button:hover::after {
    opacity: 1;
    animation: button-ripple 0.8s cubic-bezier(0, 1, 0.6, 1) forwards;
  }
}
@keyframes button-ripple {
  0% {
    opacity: 1;
    scale: 1;
  }
  100% {
    opacity: 0;
    scale: 3.5;
  }
}
.footer {
