.footer__sns {
  gap: 1.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 11.25rem;
  margin-left: auto;
  margin-bottom: 1.8125rem;
}

.footer__sns-item {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), filter 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__sns-item-img {
  object-fit: contain;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), filter 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .footer__sns-item:hover {
    transform: scale(1.2) translateY(-0.1875rem);
  }
  .footer__sns-item:hover .footer__sns-item-img {
    filter: brightness(1.2);
  }
}
.footer__nav {
  align-items: flex-start;
  display: flex;
  gap: 1.875rem;
  justify-content: center;
  padding: 3.75rem 0;
}

.footer__nav-item {
  align-items: center;
  display: flex;
  gap: 0.5rem;
  padding: 0.625rem 0;
  transition: gap 0.3s cubic-bezier(0.4, 0, 0.2, 1), transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.footer__nav-item .circle-arrow {
  aspect-ratio: 1;
  width: 1rem;
  border-radius: 50%;
  background-color: var(--white);
  display: grid;
  place-content: center;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1), transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.footer__nav-item .circle-arrow::after {
  content: "";
  display: block;
  aspect-ratio: 1;
  width: 0.875rem;
  mask-image: url("../images/arrow-right.svg");
  mask-repeat: no-repeat;
  mask-size: contain;
  background-color: var(--brown);
  mask-position: center;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__nav-item-text {
  color: var(--white);
  font-family: var(--base-font-family);
  font-size: 1rem;
  font-weight: var(--bold);
  line-height: 1.5;
  transition: color 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .footer__nav-item:hover {
    transform: translateX(0.1875rem);
  }
  .footer__nav-item:hover .circle-arrow {
    background-color: var(--yellow);
  }
  .footer__nav-item:hover .circle-arrow::after {
    background-color: var(--white);
  }
  .footer__nav-item:hover .footer__nav-item-text {
    color: var(--yellow);
  }
}
.footer__copyright {
