}
.slider__item-link .circle-arrow::after {
  content: "";
  display: block;
  aspect-ratio: 11/6;
  width: 0.75rem;
  mask-image: url("../images/arrow.svg");
  mask-repeat: no-repeat;
  mask-size: contain;
  mask-position: center;
  background-color: var(--base-color);
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1), transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .slider__item:hover .slider__item-img img {
    transform: scale(1.05);
  }
  .slider__item:hover .slider__item-link {
    gap: 0.9375rem;
    color: var(--yellow);
  }
  .slider__item:hover .slider__item-link .circle-arrow {
    border-color: var(--yellow);
    background-color: var(--yellow);
    transform: scale(1.1);
  }
  .slider__item:hover .slider__item-link .circle-arrow::after {
    background-color: var(--white);
    transform: translateX(0.125rem);
  }
}
.useful-info {
  margin-bottom: 7.5rem;
}

.useful-info__inner {
  max-width: 89.5rem;
  margin-inline: auto;
}

.useful-info__title {
  font-family: var(--base-font-family);
  font-size: 3.125rem;
  font-weight: var(--bold);
  letter-spacing: 0.01em;
  line-height: 1;
  margin-bottom: 9.375rem;
  text-align: center;
}

.useful-info__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.useful-info__item {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 12.5rem;
  justify-content: center;
  padding: 0.625rem;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.useful-info__item-icon {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.useful-info__item--app {
  background-color: #beba3f;
}

.useful-info__item--sns {
  background-color: var(--teal);
}

.useful-info__item--magazine {
  background-color: var(--green);
}

.useful-info__item--calendar {
  background-color: var(--pink);
}

.useful-info__item--contact {
  background-color: var(--purple);
}

.useful-info__img {
  aspect-ratio: 1;
  width: 6.625rem;
  margin-bottom: 0.625rem;
}

.useful-info__img img {
  object-fit: contain;
}

.useful-info__item-title {
  color: var(--white);
  font-family: var(--base-font-family);
  font-size: 2rem;
  font-weight: var(--bold);
  letter-spacing: 0.01em;
  line-height: 1.2;
  text-align: center;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .useful-info__item:hover {
    transform: translateY(-0.5rem) scale(1.02);
    box-shadow: 0 0.75rem 1.5rem rgba(0, 0, 0, 0.2);
  }
  .useful-info__item:hover .useful-info__item-icon {
    transform: scale(1.15);
  }
  .useful-info__item:hover .useful-info__item-title {
    transform: scale(1.05);
  }
}
.youtube {
  margin-bottom: 9.25rem;
  position: relative;
  max-width: 60.5rem;
  padding: 0 var(--padding-pc);
  margin-inline: auto;
  display: grid;
  place-content: center;
}

.youtube__bg {
  width: 100%;
  aspect-ratio: 918/427;
  border-radius: 2rem;
  overflow: hidden;
}

.youtube__bg-img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.youtube__button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8rem;
  aspect-ratio: 1;
  cursor: pointer;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), filter 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.youtube__icon {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .youtube__button:hover {
    transform: translate(-50%, -50%) scale(1.15);
    filter: brightness(1.1);
  }
  .youtube__button:hover .youtube__icon {
    transform: scale(1.1);
  }
}